<script>
import Talent from "./Talent.vue";
import Utility from "./Utility.vue";
import Power from "./Power.vue";
import { saveAs } from 'file-saver';
export default {
  components:{
    Talent,
    Utility,
    Power
  },
  name: 'Chatacter',
  props: {
    msg: String
  },
  data:function(){return{
      character:{
        name:"Character's Name",
        level:1,
        key:0,
        precision:0,
        agility:0,
        body:0,
        mind:0,
        speed:6,
        classA:"",
        classB:"",
        armor:0,
        powers:[],
        utilities:[],
        talents:[]
      },
      toEdit:null,
      editTalent:false,
      editPower:false,
      editUtility:false
  }},
  computed:{
    attack(){ return +this.character.precision + +this.proficiency},
    initiative(){return +this.character.agility},
    proficiency(){ return this.character.level<12?
      1+Math.ceil(this.character.level/4):this.character.level<19?
      5:this.character.level<25?
      6:this.character.level<29?7:
      8},
    armorType(){switch(this.character.armor){
      case "1": return this.character.agility;
      case "2": return this.character.body;
      case "3": return this.character.mind;
      default: return 0;
    }},
    AC(){return +8 + +this.armorType + +this.proficiency},
    tier(){return Math.ceil(this.character.level/10)},
    //maxHP(){return (50+(this.body*10))*this.tier},
    maxHP(){return 100*this.tier},
    keystat(){switch(this.character.key){
      case 1: return this.character.agility;
      case 2: return this.character.body;
      case 3: return this.character.mind;
      default: return 0;
    }},
    keyAbi(){ return ["","agility","body","mind"][this.character.key]},
    armorAbi(){ return ["","agility","body","mind"][this.character.armor]},
    fortitude(){ return +8 + +this.character.body + +this.proficiency },
    reflex(){ return +8 + +this.character.agility + +this.proficiency },
    will(){ return +8 + +this.character.mind + +this.proficiency },
    savingThrow(){ return +10 + +this.keystat },
    abiInvested(){ return +9+(+this.character.level)-(+this.character.precision + +this.character.agility + +this.character.body + +this.character.mind)}
  },
  methods:{
    setKey(i){
      this.character.key=(this.character.key==i)?0:i;
    },
    addTalent(){
      this.character.talents.push({id:this.character.talents.length,name:"Talent",tier:1,description:"Description..."})
    },
    addUtility(){
      this.character.utilities.push({id:this.character.utilities.length,name:"Utility",type:"Minor Utility",tier:1,description:"Description..."})
    },
    addPower(){
      this.character.powers.push({
      id:this.character.powers.length,
      name:"Power",
      tier:1,
      description:"Description...",
      usage:"At-Will",
      shape:"Shape",
      keywords:["Keyword 1","Keyword 2"],
      action:"",
      target:"Target",
      attack:"Attack",
      hit:"Hit",
      miss:"Miss",
      aftereffects:"",
      effects:"",
      special:""
    })
    },
    discardEdit(type){
      switch(type){
        case 1:
          this.editTalent=false;
        break;
        case 2:
          this.editPower=false;
        break;
        case 3:
          this.editUtility=false;
        break;
      }
      this.toEdit=null
    },
    raiseEdit(i,type){
      switch(type){
        case 1:
          this.editTalent=true;
          this.toEdit=this.character.talents.find(t=>t.id==i)
        break;
        case 2:
          this.editPower=true;
          this.toEdit=this.character.powers.find(p=>p.id==i)
        break;
        case 3:
          this.editUtility=true;
          this.toEdit=this.character.utilities.find(u=>u.id==i)
        break;
      }
      this.toEdit={...this.toEdit}
    },
    saveEdit(i,type){
      switch(type){
        case 1:
          this.character.talents[this.character.talents.findIndex(t=>t.id==i)]=this.toEdit
          this.editTalent=false;
        break;
        case 2:
          this.character.powers[this.character.powers.findIndex(p=>p.id==i)]=this.toEdit
          this.editPower=false;
        break;
        case 3:
          this.character.utilities[this.character.utilities.findIndex(u=>u.id==i)]=this.toEdit
          this.editUtility=false;
        break;
      }
    },
    log(x){ console.log(x)},
    saveCharacter(){
      saveAs(new Blob([JSON.stringify(this.character)],{type: "application/json"}),this.character.name+".fpf")
    },
    async loadCharacter(){
      let fileHandle;
      fileHandle = await window.showOpenFilePicker({ multiple: false })
      const file = await fileHandle[0].getFile();
      const contents = await file.text()
      this.character = JSON.parse(contents);
    }
  }
}
</script>

